// Tech-paper/PDF style for LLM Development site
// Professional, typography-focused design with no decorative elements

// Base typography with large serif font
body {
  font-family: 'Bookman Old Style', 'Bookman', 'Georgia', 'Times New Roman', serif;
  font-size: 18px;
  line-height: 1.6;
  color: #2c2c2c;
  background-color: #ffffff;
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem 3rem;
}

// Header styling
.site-header {
  border-bottom: 2px solid #2c2c2c;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
  
  .site-title {
    font-size: 28px;
    font-weight: bold;
    letter-spacing: 0.5px;
    color: #2c2c2c;
    text-decoration: none;
    
    &:hover {
      text-decoration: none;
      color: #2c2c2c;
    }
  }
  
  .site-nav {
    line-height: 2;
    
    .page-link {
      color: #2c2c2c;
      margin-left: 1.5rem;
      text-decoration: none;
      border-bottom: 1px solid transparent;
      
      &:hover {
        border-bottom: 1px solid #2c2c2c;
      }
    }
  }
}

// Footer styling
.site-footer {
  border-top: 2px solid #2c2c2c;
  margin-top: 3rem;
  padding-top: 1.5rem;
  font-size: 16px;
  color: #666;
}

// Main content area
.page-content {
  padding: 2rem 0;
}

// Heading hierarchy
h1, h2, h3, h4, h5, h6 {
  font-family: 'Bookman Old Style', 'Bookman', 'Georgia', 'Times New Roman', serif;
  color: #2c2c2c;
  font-weight: bold;
  line-height: 1.3;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

h1 {
  font-size: 36px;
  border-bottom: 3px solid #2c2c2c;
  padding-bottom: 0.5rem;
  margin-top: 0;
  margin-bottom: 1.5rem;
}

h2 {
  font-size: 28px;
  border-bottom: 2px solid #666;
  padding-bottom: 0.4rem;
  margin-top: 2.5rem;
}

h3 {
  font-size: 22px;
  margin-top: 2rem;
}

h4 {
  font-size: 20px;
  font-style: italic;
}

h5 {
  font-size: 18px;
  font-weight: normal;
  font-style: italic;
}

h6 {
  font-size: 18px;
  font-weight: normal;
}

// Paragraphs and text spacing
p {
  margin-bottom: 1.2rem;
  text-align: justify;
  hyphens: auto;
}

// Links - simple and clear
a {
  color: #2c2c2c;
  text-decoration: underline;
  
  &:hover {
    text-decoration: none;
  }
}

// Strong and emphasis
strong, b {
  font-weight: bold;
}

em, i {
  font-style: italic;
}

// Lists
ul, ol {
  margin: 1rem 0 1.5rem 2rem;
  
  li {
    margin-bottom: 0.5rem;
  }
}

// Code blocks - monospace but matching style
pre, code {
  font-family: 'Courier New', 'Courier', monospace;
  background-color: #f5f5f5;
  border: 1px solid #d0d0d0;
}

pre {
  padding: 1rem;
  overflow-x: auto;
  line-height: 1.5;
  margin: 1.5rem 0;
}

code {
  padding: 0.2em 0.4em;
  font-size: 16px;
}

pre code {
  border: none;
  padding: 0;
  background-color: transparent;
}

// Blockquotes - academic style
blockquote {
  margin: 1.5rem 2rem;
  padding-left: 1.5rem;
  border-left: 4px solid #666;
  font-style: italic;
  color: #4a4a4a;
}

// Tables - clean and readable
table {
  width: 100%;
  border-collapse: collapse;
  margin: 1.5rem 0;
  font-size: 17px;
  
  th, td {
    border: 1px solid #666;
    padding: 0.75rem 1rem;
    text-align: left;
  }
  
  th {
    background-color: #f5f5f5;
    font-weight: bold;
  }
  
  tr:nth-child(even) {
    background-color: #fafafa;
  }
}

// Navigation cards - simplified without decorative elements
.nav-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin: 2rem 0;
}

.nav-card {
  border: 2px solid #2c2c2c;
  padding: 1.5rem;
  background: #ffffff;
  
  h3 {
    margin-top: 0;
    margin-bottom: 0.75rem;
    font-size: 22px;
    border-bottom: none;
    
    a {
      text-decoration: none;
      color: #2c2c2c;
      
      &:hover {
        text-decoration: underline;
      }
    }
  }
  
  p {
    margin: 0;
    font-size: 17px;
    text-align: left;
  }
}

// Horizontal rules
hr {
  border: none;
  border-top: 2px solid #666;
  margin: 2rem 0;
}

// Small text (footnotes, etc.)
small {
  font-size: 15px;
  color: #666;
}

// Wrapper to ensure content doesn't stretch too wide
.wrapper {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
}

// Responsive adjustments
@media screen and (max-width: 800px) {
  body {
    padding: 1rem 1.5rem;
    font-size: 17px;
  }
  
  h1 {
    font-size: 30px;
  }
  
  h2 {
    font-size: 24px;
  }
  
  h3 {
    font-size: 20px;
  }
  
  .nav-cards {
    grid-template-columns: 1fr;
  }
}
